.newpassword {
  background-color: #f4f8ff;
  padding: 190px 0;

  .container12 {
    .form {
      width: 600px;
      background-color: #ffffff;
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      margin: 0 auto;
      margin-top: 20px;
      padding: 30px;
      color: #222d44;

      h3 {
        font-weight: 600;
        font-size: 22px;
        line-height: 150%;
        color: #222d44;
        margin-bottom: 20px;
      }

      form {
        ul {
          display: flex;
          flex-direction: column;
          align-items: center;

          li {
            margin-bottom: 30px;
            display: flex;
            flex-direction: row;
            align-items: center;

            &:first-child {
              margin-left: 40px;
            }

            &:last-child {
              margin-bottom: 10px;
            }

            label {
              margin-right: 20px;
            }

            input {
              width: 270px;
              height: 40px;
              background-color: #ffffff;
              box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
              border-radius: 10px;
              padding: 20px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .newpassword {
    .container12 {
      .form {
        width: 90%;
        text-align: center;

        form {
          ul {
            li {
              margin-bottom: 30px;
              display: flex;
              flex-direction: column;
              justify-content: center;

              &:first-child {
                margin-left: 0px;
              }

              .label {
                margin-bottom: 15px;
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .newpassword {
    .container12 {
      .form {
        form {
          ul {
            li {
              .input-text {
                width: 120%;
              }
            }
          }
        }
      }
    }
  }
}
