@media (max-width: 768px) {
  .home {
    .container {
      padding: 0 15px;

      .what-clinic-slider {
        flex-direction: column;
        margin-right: 20px;

        .what-clinic-text {
          width: 100%;
        }

        .what-clinic-mini {
          width: 100%;
        }
      }

      #what-clinic {
        flex-direction: column;
        margin: 10px;

        .what-clinic-mini,
        .what-clinic-text {
          width: 100%;
          margin: 10px 0;
        }
      }

      #statistic-clinic {
        .statistic-row {
          flex-direction: column;
          width: 100%;
          margin-top: 15px;

          .statistic-items {
            width: 100%;

            .statistic {
              max-width: 70%;
              margin-bottom: 15px;

              h3 {
