.table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

.table td, .table th {
    border: 1px solid #ddd;
    padding: 8px;
}

.table tr:nth-child(even) {
    background-color: #f2f2f2;
}

.table tr:hover {
    background-color: #ddd;
}

.table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
}
.navlanguage {
    height: 40px;
    margin: 0 20px;
    z-index: 1000;
}


.navlanguage-item {
    background-color: white;
    height: 40px;

    border-radius: 30px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    padding: 11px 10px;
}

.navlanguage-content {
    display: none;
    position: absolute;
    background-color: white;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 1;
    border-radius: 0 0 20px 20px;
}

.navlanguage-content a {
    color: black;
    text-decoration: none;
    display: block;
    padding: 10px;
}

.navlanguage-content a:hover {
    background-color: rgba(0, 0, 0, 0.1);
    text-decoration: none;
    display: block;
    padding: 10px;
}


.navlanguage:hover .navlanguage-content {
    display: block;
}

.navlanguage:hover .navlanguage-item {
    border-radius: 20px 20px 0 0;
}


