/* ================================================== */
/* 01. General Style */
/* ================================================== */

body {
    font-family: Poppins, sans-serif;
    font-size: 14px;
    color: #2a2a2a;
    line-height: 24px;
    font-weight: 400;
}

/*---Typography---*/

h1, h2, h3, h4, h5, h6 {
    padding: 0px;
    margin: 0px;
    color: #ff2f2f;
}

h1 {
    font-size: 46px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 30px;
}

h4 {
    font-size: 24px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 14px;
}

p {
    padding: 0px;
    margin: 0px;
}

ol, ul {
    margin-bottom: 0px;
}

a {
    text-decoration: none !important;
    outline: none;
    transition: all 0.3s ease 0s;
}

img {
    max-width: 100%;
}

label {
    font-weight: normal;
}

::-webkit-input-placeholder {
    opacity: 1;

}

:-moz-placeholder { /* Firefox 18- */
    opacity: 1;

}

::-moz-placeholder { /* Firefox 19+ */
    opacity: 1;

}

:-ms-input-placeholder {
    opacity: 1;

}

input:focus, textarea:focus, select:focus {
    outline: none;
}

/*-----------------------
	Page Loader
-----------------------*/

#loading {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 99999;
    background: #fff;
}

#loading .element {
    position: absolute;
    margin: 0 auto;
    left: 0px;
    right: 0px;
    top: 50%;
}

.sk-folding-cube {
    margin: 20px auto;
    width: 40px;
    height: 40px;
    position: relative;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
    float: left;
    width: 50%;
    height: 50%;
    position: relative;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.sk-folding-cube .sk-cube:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ff2f2f;
    -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
    animation: sk-foldCubeAngle 2.4s infinite linear both;
    -webkit-transform-origin: 100% 100%;
    transform-origin: 100% 100%;
}

.sk-folding-cube .sk-cube2 {
    -webkit-transform: scale(1.1) rotateZ(90deg);
    transform: scale(1.1) rotateZ(90deg);
}

.sk-folding-cube .sk-cube3 {
    -webkit-transform: scale(1.1) rotateZ(180deg);
    transform: scale(1.1) rotateZ(180deg);
}

.sk-folding-cube .sk-cube4 {
    -webkit-transform: scale(1.1) rotateZ(270deg);
    transform: scale(1.1) rotateZ(270deg);
}

.sk-folding-cube .sk-cube2:before {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}

.sk-folding-cube .sk-cube3:before {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
}

.sk-folding-cube .sk-cube4:before {
    -webkit-animation-delay: 0.9s;
    animation-delay: 0.9s;
}

@-webkit-keyframes sk-foldCubeAngle {

    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
        transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    }
    25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
        transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    }
    90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
        transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
}

@keyframes sk-foldCubeAngle {

    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
        transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    }
    25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
        transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    }
    90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
        transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
}

/* ================================================== */
/* 02. header	*/
/* ================================================== */

header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
    left: 0;
    transition: all 0.3s ease 0s;
}

.header-middle {
    padding-top: 24px;
    padding-bottom: 20px;
}

header .logo img {
    max-width: 100%;
    transition: all 0.3s ease 0s;
}

.header-right-top {
    text-align: right;
    /*border-bottom: 1px solid #b9b9b9;*/
    padding: 4px 0 25px;
    transition: all 0.3s ease 0s;
}

.header-right-top a {
    text-decoration: none;
    font-size: 17px;
    font-weight: 500;
    line-height: 24px;
}

.header-right-top a.tel-number {
    color: #2a2a2a;
    margin-right: 20px;
    position: relative;
}

.header-right-top a.tel-number:hover {
    text-decoration: none;
}

.header-right-top a.tel-number .fa.fa-phone {
    font-size: 28px;
    position: absolute;
    left: -34px;
    top: 0px;
}

.header-right-top a.free-consultation_btn {
    color: #fff;
    text-transform: uppercase;
    background: #ff2f2f;
    padding: 10px 24px;
    border-radius: 24px;
}

.header-right-top a.free-consultation_btn:hover {
    background: #0267a2;
}

/* ================================================== */
/* A. Header Small */
/* ================================================== */
header.header-small {
    background: #fff;
    box-shadow: 1px 1px 10px #000;
}

header.header-small .header-middle {
    padding-top: 15px;
    padding-bottom: 0;
}

header.header-small .logo img {
    max-width: 310px;
}

header.header-small .header-right-top {
    padding: 4px 0 15px;
}

header.header-small .header-right-top a {
    font-size: 15px;
}

header.header-small .header-right-top a.free-consultation_btn {
    padding: 8px 20px;
}


/* ================================================== */
/* 03. navigation */
/* ================================================== */

.navbar {
    border-radius: 0px;
    margin: 0px;
    border: none;
    padding: 0;
    min-height: auto;
    transition: all 0.3s ease 0s;
}

.navbar-collapse {
    padding: 0px;
}

.navbar-inverse {
    background: none;
}

.navbar-nav > li {
    border-right: 0;
}

.navbar-nav > li > a {
    text-transform: uppercase;
}

.navbar-nav > li:last-child {
    border: none;

}

.navbar-dark .navbar-nav li:last-child a {
    padding-right: 0;
}

.navbar-dark .navbar-nav li:first-child a {
    padding-left: 0;
}

.navbar-dark .navbar-nav .nav-link {
    font-size: 16px;
    color: #2a2a2a;
    padding: 10px 15px;
}

.navbar-dark .navbar-nav .active > .nav-link, .navbar-dark .navbar-nav .nav-link.active, .navbar-dark .navbar-nav .nav-link.show, .navbar-dark .navbar-nav .show > .nav-link, .navbar-dark .navbar-nav .nav-link:focus, .navbar-dark .navbar-nav .nav-link:hover {
    color: #ff2f2f;
    background-color: transparent;
}

.navbar-inverse .navbar-nav > li > a i {
    font-size: 20px;
    color: #8bd29d;
}

.navbar-inverse .navbar-nav > .open > a, .navbar-inverse .navbar-nav > .open > a:focus, .navbar-inverse .navbar-nav > .open > a:hover {
    background: #4ab866;
}

.dropdown:hover > .dropdown-menu {
    display: block;
}

.dropdown-menu {
    margin: 0;
    border-top: 4px #ff2f2f solid;
    background: #fff;

}

.dropdown-item {
    font-size: 16px;
}

.dropdown-menu:after {
    bottom: 100%;
    left: 40px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    border-color: rgba(255, 150, 0, 0);
    border-bottom-color: #ff2f2f;
    border-width: 10px;
}

