.navbar-wrapper {
  .navbarr {
    display: flex;
    flex-direction: column;
    width: 320px;
    padding: 30px;
    background-color: #ffffff;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 10px;

    .ant-badge {
      width: 100%;
    }

    ul {
      .navbar-items {
        height: 40px;
        width: 100%;
        background-color: #ffffff;
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
        border-radius: 30px;
        border: 2px solid #ffffff;

        &:not(:last-child) {
          margin-bottom: 20px;
        }

        a {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100%;
          width: 100%;
          text-decoration: none;
          font-size: 16px;
          font-weight: 500;
          color: #222d44;
          text-align: center;
        }
      }

      #active {
        border: 2px solid #222d44a2;
      }
    }
  }
}
