.account-moderator {
  padding: 100px 0;
  background-color: #f4f8ff;

  .container12 {
    .section-body {
      .content-wrapper {
        width: 100%;

        .moderator-heads-department-section {
          .send-list {
            max-width: 250px;
          }

          .moderator-heads-department-item {
            .content {
              .fedbeck {
                flex-wrap: wrap;

                .user-item {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }

        .moderator-performer-section {
          .moderator-performer-item {
            .content {
              .avatar {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
              }
            }
          }
        }

        .moderator-listnear-section {
          .moderator-listnear-item {
            .content {
              .fedbeck {
                flex-wrap: wrap;

                .user-item {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }

        .moderator-applicant-section {
          .moderator-applicant-item {
            .content {
              .fedbeck {
                flex-wrap: wrap;

                .request-content-title-name {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }

        .moderator-appeals-section {
          .content-top {
            flex-wrap: wrap;
            justify-content: space-around;

            .request-items,
            .request-search,
            .request-sort {
              margin-bottom: 10px;
            }
          }
        }

        .moderator-fedbeck-request-section {
          .supervisor-feedbak-request-dizz,
          .supervisor-feedbak-request-item {
            .content {
              .avatar {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                margin-bottom: 15px;

                .red-btn {
                  border: 2px solid #e91f1f;
                  box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
                  border-radius: 30px;
                  color: #e91f1f;
                  background: white;
                  padding: 8px 12px;
                }

                .green-btn {
                  border: 2px solid #63aa55;
                  box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
                  border-radius: 30px;
                  color: #63aa55;
                  background: white;
                  padding: 8px 12px;
                }
              }
            }
          }
        }

        .moderator-listnear-reyting-section {
          .moderator-listnear-reyting-item {
            .content {
              .fedbeck {
                .reyting-stars {
                  display: flex;
                  flex-direction: row;
                  align-items: center;

                  img {
                    margin: 0 3px;
                  }
                }
              }
            }
          }
        }

        .supervisor-statistic-section {
          .responsive-nav-top {
            display: none;
          }
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .account-moderator {
    .container12 {
      .section-body {
        padding: 50px 13px;
      }
    }
  }
}

@media (max-width: 992px) {
  .account-moderator {
    .container12 {
      .section-body {
        display: flex;
        padding: 50px 0;
        flex-direction: column;

        .navbar-wrapper {
          width: 100%;
          margin-bottom: 15px;

          .navbarr {
            width: 100%;
            padding: 20px 10px;

            ul {
              display: flex;
              flex-direction: row;
              flex-wrap: wrap;
              width: 100%;

              li {
                max-width: 46%;
                margin: 7px 12px;

                a {
                  font-size: 14px;
                }
              }
            }
          }
        }

        .content-wrapper {
          margin-left: 0px;
        }
      }
    }
  }
}

