            .responsive-nav-top {
              display: block;
              border-radius: 20px;
              box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
              margin: 20px auto;
              width: 240px;
              padding: 10px;
            }
          }
        }
      }
    }
  }
