@media (max-width: 576px) {
  .account-moderator {
    .container12 {
      padding: 0 20px;

      .section-body {
        .content-wrapper {
          .moderator-listnear-section {
            .moderator-listnear-item {
              .content {
                .fedbeck {
                  .user-item {
                    .user-person-inform {
                      flex-direction: column;
                      align-items: center;

                      .user-img {
                        margin-bottom: 10px;
                      }

                      .user-inform {
                        text-align: center;
                      }
                    }
                  }
                }
              }
            }
          }

          .moderator-appeals-section {
            .moderator-appeals-item {
              .content {
                text-align: center;

                .document-prosses {
                  margin-bottom: 30px;

                  .new,
                  .progressing,
                  .close {
                    width: 100px;

                    .prosses-item {
                      height: 20px;
                      width: 20px;

                      .prosses-item-active {
                        height: 15px;
                        width: 15px;
                      }
                    }
                  }

                  .line {
                    margin-top: 15px;
                  }

                  h3 {
                    font-size: 14px;
                  }
                }

                .request-content {
                  .request-content-title {
                    .request-content-title-name {
                      margin-top: 20px;
                      flex-direction: column;
                      align-items: center;
                      margin: 0 auto;

                      .applicant-image {
                        margin: 50px 0 10px 0;
                      }

                      .id {
                        position: absolute;
                        top: 5px;
                        left: 0;
                      }
                    }
                  }

                  .request-theme {
                    flex-wrap: wrap;

                    h3 {
                      margin-bottom: 10px;
                    }
                  }
                }
              }
            }
          }

          .moderator-fedbeck-request-section {
            .supervisor-feedbak-request-dizz,
            .supervisor-feedbak-request-item {
              .content {
                .user-item {
                  justify-content: center;
                }

                .avatar {
                  flex-direction: column;
                  align-items: center;

                  .request-content-title-name {
                    margin-bottom: 15px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

