.personal-account-supervisor {
  padding: 100px 0;
  background-color: #f4f8ff;

  .container12 {
    .section-body {
      .content-wrapper {
        width: 100%;

        .supervisor-response-section {
          .supervisor-response-request-item {
            .content {
              .request-categoriyes {
                margin: 15px 0;
              }

              .request-bottom {
                margin-top: 25px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: flex-start;

                .file-upload {
                  margin-bottom: 0;
                }

                .green-btn {
                  border: 2px solid #63aa55;
                  box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
                  border-radius: 30px;
                  color: #63aa55;
                  background: white;
                  padding: 8px 12px;
                }
              }
            }
          }
        }

        .supervisor-incoming-request-section {
          .supervisor-incoming-request-item {
            .content {
              .fedbeck {
                flex-wrap: wrap;

                .user-item {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }

        .supervisor-appeal-section {
          .supervisor-appeal-item {
            .content {
              .request-categoriyes {
                margin: 15px 0;
              }

              .request-bottom {
                margin-top: 25px;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: flex-start;

                .file-upload {
                  margin-bottom: 0;
                }
              }
            }
          }
        }

        .supervisor-applicant-section {
          .supervisor-applicant-item {
            .content {
              .fedbeck {
                flex-wrap: wrap;

                .request-content-title-name {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }

        .supervisor-feedbak-request-section {
          .supervisor-feedbak-request-dizz,
          .supervisor-feedbak-request-item {
            .content {
              .avatar {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                margin-bottom: 15px;

                .red-btn {
                  border: 2px solid #e91f1f;
                  box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
                  border-radius: 30px;
                  color: #e91f1f;
                  background: white;
                  padding: 8px 12px;
                }

                .green-btn {
                  border: 2px solid #63aa55;
                  box-shadow: (0px 0px 15px rgba(0, 0, 0, 0.1));
                  border-radius: 30px;
                  color: #63aa55;
                  background: white;
                  padding: 8px 12px;
                }
              }
            }
          }
        }

        .supervisor-reyting-listnear-section {
          .supervisor-reyting-listnear-item {
            .content {
              .fedbeck {
                .reyting-stars {
                  display: flex;
                  flex-direction: row;
                  align-items: center;

                  img {
                    margin: 0 3px;
                  }
                }
              }
            }
          }
        }

        .supervisor-statistic-section {
          .responsive-nav-top {
            display: none;
          }
        }
      }
    }
  }
}

@media (max-width: 1200px) {
  .personal-account-supervisor {
    .container12 {
      .section-body {
        padding: 50px 13px;

        .content-wrapper {
          .peraccsup-listeners-section {
            .listnears-section-top {
              flex-wrap: wrap;
              justify-content: space-around;

              .list-generation,
              .send-list,
              .request-sort {
                margin-bottom: 10px;
                max-width: 250px;
              }
            }
          }

          .supervisor-response-section {
            .supervisor-response-request-item {
              .content {
                .request-bottom {
                  .file-upload {
                    .file {
                      max-width: 230px;
                    }
                  }
                }
              }
            }
          }

          .supervisor-appeal-section {
            .supervisor-appeal-item {
              .content {
                .request-bottom {
                  .file-upload {
                    .file {
                      max-width: 230px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media (max-width: 992px) {
  .personal-account-supervisor {
    .container12 {
      .section-body {
        display: flex;
        padding: 50px 0;
        flex-direction: column;

        .navbar-wrapper {
          width: 100%;
          margin-bottom: 15px;

          .navbarr {
            width: 100%;
            padding: 20px 10px;

            ul {
              display: flex;
              flex-direction: row;
              flex-wrap: wrap;
              width: 100%;

              li {
                max-width: 46%;
                margin: 7px 12px;

                a {
                  font-size: 14px;
                }
              }
            }
          }
        }

        .content-wrapper {
          margin-left: 0px;
        }
      }
    }
  }
}

